@keyframes appear {
  0% {
    opacity: 0;
    top: 35px;
  }

  100% {
    opacity: 1;
    top: 0;
  }
}

.wrapper {
  list-style: none;
  padding: 50px 30px;
  display: flex;
  align-items: center;
  position: relative;
  animation: appear .5s ease;
}

.image {
  margin-right: 30px;
  flex-shrink: 0;
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.name {
  margin: 0;
  color: blue;
  font-weight: 700;
}

.description {
  margin: 10px 0 20px;
  font-weight: 400;
}

.button {
  font-size: 12px;
  text-decoration: none;
  padding: 7px 12px;
  font-weight: 500;
  background: none;
  border: 2px solid blue;
  color: blue;
}